@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.skill {
    width: 100%;
    height: 100vh;
    color: #fff;
    padding-top: 4.5rem;

    &__container {
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        display: flex;
        flex-direction: column;

        &-title {
            text-transform: uppercase;
            font-size: 2rem;

            h1 {
                font-family: 'Press Start 2P', cursive;
                letter-spacing: 5px;
            }
        }

        &-content {
            margin-top: 4rem;
            padding: 0 1rem;

            .spinner {
                text-align: center;
                width: 100%;
                // height: 20rem;
                display: flex;
            }

            .text__spin {
                font-size: 2.5rem;
                letter-spacing: 2px;
                -webkit-text-fill-color: rgb(0, 0, 0);
                -webkit-text-stroke-width: 1px;
                -webkit-text-stroke-color: rgba(255, 255, 255, 0.8);

                h1 {
                    margin: 0 1rem;
                    opacity: 0.2;
                    transition: .5s;
                }
            }
        }
    }
}

.skill .is-active>.text__spin h1 {
    opacity: 1;
    cursor: pointer;
    -webkit-text-fill-color: #fff;
    -webkit-text-stroke-width: none;
    -webkit-text-stroke-color: transparent;
    transform: translate(-15px, -15px);
    text-shadow: 0 0 10px #fff;
}

.text__spin:hover>h1 {
    opacity: 1;
    cursor: pointer;
    -webkit-text-fill-color: #fff;
    -webkit-text-stroke-width: none;
    -webkit-text-stroke-color: transparent;
    transform: translate(-15px, -15px);
    text-shadow: 0 0 10px #fff;
}

.skill .splide__arrows {
    display: none;
}