* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.App {
  width: 100%;
  background: #000;
}

/* width */

::-webkit-scrollbar {
  width: 7px;
}

/* Track */

::-webkit-scrollbar-track {
  background: none;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: rgb(226, 221, 221);
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: rgb(226, 221, 221);
}

#tsparticles {
  position: absolute;
  top: 0;
  width: 100%;
  height: 600vh;
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.container .content {
  /* position: absolute; */
  z-index: 1;
}