@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

.gallery {
    width: 100%;
    height: 100vh;
    color: #fff;
    padding-top: 8.5rem;

    &__container {
        overflow: auto;
        width: 100%;
        display: flex;
        justify-content: center;

        &-content {
            box-sizing: border-box;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
        }

        &-gallery {
            display: flex;
            width: 90%;
            margin-left: 4rem;
        }

        .box {
            margin: 0 0.2rem;
            width: 15rem;
            height: 30rem;
            transition: .5s;

            img {
                height: 100%;
                width: 100%;
                transition: all .5s ease-in-out;
                object-fit: cover;
                filter: grayscale(100%);
            }
        }

        .box:hover {
            cursor: zoom-in;
        }

        .box:hover>img,
        .box:hover>a img {
            filter: grayscale(0);
            width: 100%;
            transform: scale(1.1);
        }

        &-title {

            h1 {
                margin-bottom: 1rem;
                color: #ffffff98;
                font-size: 3rem;
                font-family: 'Poppins', sans-serif;
                text-transform: uppercase;
                letter-spacing: 5px;
            }

            h1 span,
            h1 {
                cursor: none;
                transition: .5s;
            }

            h1:hover span:nth-child(1) {
                margin-right: 10px;
            }

            h1:hover span:nth-child(1)::after {
                content: "'";
            }

            h1:hover span:nth-child(2) {
                margin-left: 20px;
            }

            h1:hover span {
                color: #fff;
                text-shadow: 0 0 10px #fff,
                    0 0 20px #fff;
            }

        }
    }
}

.gallery .splide__pagination {
    display: none;
}

.css-luqs5k p {
    letter-spacing: 4px;
}