.navbar_left {
    position: fixed;
    padding-top: 3rem;
    left: 0.4rem;
    width: 5rem;
    height: 100vh;
    z-index: 99;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: none;

    margin-top: 5.5rem;
}

.navbar_left a {
    color: rgba(255, 255, 255);
}

.navbar_left .MuiSvgIcon-root {
    transition: .3s;
    opacity: 0.7;
    background: none;
    cursor: pointer;
    font-size: 2rem;
    margin: 1.5rem 0;
}

.navbar_left .MuiSvgIcon-root:hover {
    transform: scale(1.2);
    opacity: 1;
}