@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

.about {
    &__container {
        display: flex;
        flex-direction: row;
        height: 100vh;
        margin-top: 5.5rem;

        &-bottom {
            text-decoration: none;
            color: #34465d;
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: absolute;
            bottom: -2.1rem;
            right: 1.7rem;
            width: 12rem;
            padding-left: 1.4rem;
            height: 3rem;
            z-index: 999;
            border-radius: 50px;
            background: #fff;
            transition: .5s;
            opacity: 0.7;

            h2 {
                transition: 1.5s;
                letter-spacing: 1.5px;
                font-family: 'Poppins', sans-serif;
                opacity: 0.8;
                font-size: 1.2rem;
                // display: none;
            }

            .btn-rounded {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 3rem;
                height: 3rem;
                border-radius: 50%;
                background: #fff;
            }


            &:hover .MuiSvgIcon-root {
                transform: rotate(-130deg);
            }

            &:hover {
                cursor: pointer;
                opacity: 1;
            }

        }

        &-bottom .MuiSvgIcon-root {
            color: #34465d;
            font-size: 2rem;
            margin: 1.5rem 0;
            transition: .5s;
        }


    }

    &__left {
        width: 45%;
        height: 100vh;
    }

    &__left>img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__right {
        width: 55%;
        height: 100%;
        color: #fff;
        display: flex;
        flex-direction: column;

        &-content {
            display: flex;
            flex-direction: column;
            margin: 5rem 3rem;

            h1 {
                animation: glitch 1s linear infinite;
                color: #ffffff;
                font-size: 3rem;
                margin-bottom: 1rem;
            }

            h1:before,
            h1:after {
                content: attr(title);
                position: absolute;
                left: 0;
            }

            h1:before {
                animation: glitchTop 1s linear infinite;
                clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
                -webkit-clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
            }

            h1:after {
                animation: glitchBotom 1.5s linear infinite;
                clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
                -webkit-clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
            }

            p {
                letter-spacing: 0.2px;
                margin-bottom: 1rem;
                opacity: 0.8;
                font-size: 1.1rem;
            }

        }

        &-name {
            color: black;
            opacity: 0.85;
            -webkit-text-fill-color: rgb(0, 0, 0);
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: rgb(255, 255, 255);
            letter-spacing: 10px;
            font-size: 4rem;
            transition: 0.5;
            margin-left: 0.7rem;
        }

        &-name:hover {
            opacity: 1;
            cursor: pointer;
            -webkit-text-fill-color: #fff;
            -webkit-text-stroke-width: none;
            -webkit-text-stroke-color: transparent;
            text-shadow: 0 0 10px #fff;
        }

        &-sub_name {
            letter-spacing: 2px;
            opacity: 0.5;
            font-weight: 400;
            text-transform: uppercase;
            font-size: 1.2rem;
            margin-left: 0.8rem;
        }

        &-contact {
            letter-spacing: 2px;
            opacity: 0.5;
            font-weight: 400;
            font-size: 1.2rem;
            margin-left: 0.8rem;
        }

    }

}

@keyframes glitch {

    2%,
    64% {
        transform: translate(2px, 0) skew(0deg);
    }

    4%,
    60% {
        transform: translate(-2px, 0) skew(0deg);
    }

    62% {
        transform: translate(0, 0) skew(5deg);
    }
}

@keyframes glitchTop {

    2%,
    64% {
        transform: translate(2px, -2px);
    }

    4%,
    60% {
        transform: translate(-2px, 2px);
    }

    62% {
        transform: translate(13px, -1px) skew(-13deg);
    }
}

@keyframes glitchBotom {

    2%,
    64% {
        transform: translate(-2px, 0);
    }

    4%,
    60% {
        transform: translate(-2px, 0);
    }

    62% {
        transform: translate(-22px, 5px) skew(21deg);
    }
}