.navbar {
    position: fixed;
    display: flex;
    padding-top: 2rem;
    z-index: 9;
    // align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 3.5rem;
    // background: #fff;
    flex-direction: row;

    &__logo {
        margin-left: 1.8rem;
        color: rgb(244, 196, 72);
    }

    &__list {
        display: flex;
        color: rgba(255, 255, 255, 0.5);
        margin-right: 4rem;

        &-items {
            cursor: pointer;
            text-transform: uppercase;
            letter-spacing: 1.5px;
            margin: 0 20px;
            transition: .3s;
        }

        &-items:hover {
            color: rgb(244, 196, 72);
            display: flex;
            flex-direction: column;
            align-items: center;
            opacity: 1;
            transform: scale(1.07);
        }

    }

}

.active {
    transition: .5s;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 1;
    color: rgb(244, 196, 72);
}

.active::after {
    content: '';
    transition: .5s;
    width: 10px;
    height: 10px;
    background: #ffcd4c;
    border-radius: 50%;
    // margin-top: 2rem;
    margin: 10px;
}

.navbar .navbar__logo .MuiSvgIcon-root {
    font-size: 2rem;
    transition: .5s;
    // animation: anim 1s infinite;
}

@keyframes anim {
    0% {
        transform: rotate(360deg) scale(1.3);
    }

    100% {
        transform: rotate(0deg) scale(1);
    }
}