@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

.project {
    &-container {
        width: 100%;
        height: 100vh;
        color: rgba(255, 255, 255);
        padding-top: 8.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &-title {
        margin-bottom: 1.2rem;
        opacity: 0.5;
        font-weight: 400;
        font-family: 'Poppins', sans-serif;
        letter-spacing: 4px;
        font-size: 1.4rem;
        text-transform: uppercase;
    }

    &-title::after {
        content: '';
        position: absolute;
        width: 2.5rem;
        height: 4px;
        background: rgba(255, 255, 255);
    }

    &-title::before {
        content: '';
        position: absolute;
        width: 4.2rem;
        height: 4px;
        background: rgba(255, 255, 255);
    }

    &-content {
        width: 100%;
        margin-left: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        overflow: auto;
    }

    &__box {
        &-content {
            margin: 0.3rem;
            width: 25rem;
            height: 15rem;

            &:hover .project__box-overlay {
                cursor: pointer;
                display: flex;
            }

            &:hover .project__box-image {
                filter: grayscale(0);
                transform: scale(1.4) rotate(15deg);
            }

        }

        &-image {
            transition: .5s;
            border-radius: 20px;
            width: 25rem;
            height: 15rem;
            filter: grayscale(100%);
        }

        &-image img {
            width: 100%;
            height: 100%;
        }

        &-overlay {
            transition: all 1 ease-in-out;
            display: none;
            justify-content: center;
            align-items: center;
            position: absolute;
            width: 25rem;
            height: 15rem;
            background: rgba(24, 22, 22, 0.5);
            margin-top: -15rem;

            a .MuiSvgIcon-root {
                color: #b6c0ce;
                font-size: 2rem;
                margin: 1.5rem 0;
                transition: .5s;
            }

        }

    }

}